.Business {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 16.66rem;
  margin: 0 .5rem 2.3rem .5rem;
  color: #979797;
}

.Business .image-container {
  height: 16.66rem;
  margin-bottom: 1rem;
  border-radius: 10px;
}
.Business .image-container img {
  height: auto;
  width: 100%;
}

.Business h2 {
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 600;
}
.business-name {
  color: #f0f0f0;
}
.Business-information {
  display: flex;
  justify-content: space-between;
}

.Business-information p {
  font-size: .88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Business-address {
  flex-direction: column;
  justify-content: space-between;
}
.Business-address p {
  margin: 0px;
padding: 0px;
}
.Business-reviews {
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}

.Business-reviews h3 {
  color: #e9492a;
  font-weight: 600;
  padding: 0px;
  margin: 0px;
}
.Business-reviews .food-category {
  color: #f0f0f0;
}

.Business-reviews .rating {
  color: #ffd02e;
  font-size: .88rem;
  line-height: 1rem;
}
